<template>
  <div class="parent">
    <div>
      <transition
        enter-active-class="animate__animated animate__flipInX"
        leave-active-class="animate__animated animate__flipOutX"
      >
        <div v-if="searchBox">
          <label class="choose">Choose Your Search Type</label>

          <label class="choices">
            <input
              type="radio"
              name="checkType"
              value="id"
              v-model="checkType"
            />
            ID
          </label>
          <label class="choices">
            <input
              type="radio"
              name="checkType"
              value="name"
              v-model="checkType"
            />
            Name
          </label>

          <div style="overflow: hidden">
            <transition
              enter-active-class="animate__animated animate__fadeInTopLeft animate__fast"
              leave-active-class="animate__animated animate__fadeOutTopRight animate__fast"
              mode="out-in"
            >
              <input
                key="id"
                v-if="checkType == 'id'"
                class="checkInput"
                type="number"
                v-model="searchWithId"
                placeholder="Enter an Id"
              />
              <input
                key="name"
                v-else
                class="checkInput"
                type="text"
                v-model="searchWithName"
                placeholder="Enter a Name"
              />
            </transition>
          </div>
        </div>
      </transition>
      <div
        class="iconSwitch"
        @click="searchBox = !searchBox"
        :title="searchBox ? 'Hide Search Box' : 'Show Search Box'"
      >
        <i
          class="fas"
          :class="searchBox ? 'fa-chevron-up' : 'fa-chevron-down'"
        ></i>
      </div>
    </div>

    <transition
      enter-active-class="animate__animated animate__backInDown"
      appear
    >
      <table>
        <thead>
          <tr>
            <th>Student's ID</th>
            <th>Student's Name</th>
            <th>Update / Remove</th>
          </tr>
        </thead>

        <transition-group
          tag="tbody"
          enter-active-class="animate__animated animate__backInLeft"
          leave-active-class="animate__animated animate__backOutRight"
          mode="out-in"
        >
          <tr v-for="(stud, index) in filterResult" :key="index">
            <td>{{ stud.id }}</td>
            <td>{{ stud.name }}</td>
            <td>
              <button id="delete" @click="deleteStudent(index)">Delete</button>
              <router-link to="/">
                <button id="update" @click="sendInfoIntoMain(index)">
                  Update
                </button>
              </router-link>
            </td>
          </tr>

          <tr v-if="filterResult.length == 0" key="empty">
            <td style="padding: 20px 0; font-weight: bold;" colspan="3">
              There're no Students
            </td>
          </tr>
        </transition-group>
      </table>
    </transition>

    <transition
      enter-active-class="animate__animated animate__zoomIn animate__delay-1s"
      leave-active-class="animate__animated animate__zoomOut animate__delay-2s"
      appear
    >
      <div
        title="Clear Student List"
        v-if="studentList.length >= 2"
        class="clearStudentList"
      >
        <transition
          enter-active-class="animate__animated animate__fadeIn"
          leave-active-class="animate__animated animate__fadeOut"
          mode="out-in"
        >
          <i
            v-if="showIcon"
            key="clearIcon"
            class="fas fa-broom"
            @click="showIcon = !showIcon"
          ></i>
          <div v-else key="clearMessage">
            <span>Are you sure that you want to clear students List?</span>
            <div>
              <button @click="clearStudentList" class="clear-btn">
                I'm sure
              </button>
              <button @click="showIcon = !showIcon" class="not-clear-btn">
                Don't clear it
              </button>
            </div>
          </div>
        </transition>
      </div>
    </transition>
  </div>
</template>

<script>
import { allFunc } from "../main";

export default {
  name: "Records",

  data: function() {
    return {
      checkType: "id",
      studentList: allFunc.studentList,
      searchWithId: "",
      searchWithName: "",
      searchBox: false,
      showIcon: true
    };
  },

  beforeCreate() {
    let localInfo = JSON.parse(localStorage.getItem("myAllStudents"));
    if (localInfo != null) {
      allFunc.studentList = localInfo;
    }
  },

  computed: {
    filterStudentWithId() {
      return this.studentList.filter(el => {
        return el.id.match(this.searchWithId);
      });
    },
    filterStudentWithName() {
      return this.studentList.filter(el =>
        el.name.match(new RegExp(this.searchWithName, "i"))
      );
    },
    filterResult() {
      return this.checkType == "id"
        ? this.filterStudentWithId
        : this.filterStudentWithName;
    }
  },

  methods: {
    deleteStudent(index) {
      this.studentList.splice(index, 1);
      localStorage.setItem("myAllStudents", JSON.stringify(this.studentList));
    },
    sendInfoIntoMain(index) {
      allFunc.getInfo(index);
    },
    clearStudentList() {
      this.studentList = [];
      localStorage.setItem("myAllStudents", JSON.stringify(this.studentList));
    }
  }
};
</script>

<style lang="scss" scoped>
.parent {
  width: 100%;
  margin: 40px auto;
  & > div {
    label {
      &.choose {
        display: block;
        font-size: 22px;
        color: #009688;
        font-weight: 700;
        margin: 15px 0 25px 0;
      }
      &:not(.choose) {
        font-size: 18px;
        font-weight: bold;
        color: #3e3e3e;
        cursor: pointer;
        &:not(:last-of-type) {
          margin-right: 15px;
        }
      }
      &.choices {
        background: #3f3f3f;
        padding: 5px 12px;
        color: #fff;
        border-radius: 5px;
        input[type="radio"] {
          margin: 0 5px 0 0;
        }
      }
    }
    input {
      &.checkInput {
        width: 100%;
        display: block;
        margin: 50px 0;
        padding: 8px;
        border: 2px solid #d4d4d4;
        border-radius: 5px;
        outline: none;
      }
    }
    .iconSwitch {
      text-align: center;
      background-color: #3f3f3f;
      color: #fff;
      padding: 5px 0;
      font-size: 30px;
      cursor: pointer;
      border-radius: 5px;
    }
  }

  table {
    margin: 60px 0;
    width: 100%;
    border-spacing: 0px;
    thead {
      tr {
        th {
          background-color: #3f3f3f;
          color: #fff;
          padding: 20px 0;
          border-bottom: 1px solid #ffffff;
          line-height: 1.4;
        }
      }
    }
    tbody {
      tr {
        &:not(:last-of-type) {
          td {
            border-bottom: 1px solid #3f3f3f;
          }
        }
        td {
          text-align: center;
          padding: 10px 5px;
          background-color: #f3f3f3;
          color: #3f3f3f;
          line-height: 1.4;
          &:not(:last-of-type) {
            word-break: break-all;
          }
          button {
            border: none;
            outline: none;
            color: #fff;
            padding: 5px 20px;
            border-radius: 4px;
            margin: 5px;
            cursor: pointer;
            &#delete {
              background-color: #e91e63;
            }
            &#update {
              background-color: #009688;
            }
          }
        }
      }
    }
  }
  .clearStudentList {
    background: #e91e63;
    text-align: center;
    padding: 10px 0;
    font-size: 60px;
    color: #fff;
    border-radius: 100px;
    overflow: hidden;
    i {
      cursor: pointer;
    }
    & > div {
      padding: 10px;
      span {
        font-size: 22px;
        display: block;
        line-height: 1.4;
      }
      div {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        button {
          font-size: 16px;
          border: 0;
          outline: none;
          border-radius: 5px;
          padding: 8px 35px;
          margin: 20px 10px 0 10px;
          cursor: pointer;
          &.clear-btn {
            background: #232323;
            color: #fff;
          }
          &.not-clear-btn {
            background: #fff;
            color: #232323;
          }
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .parent {
    & > div {
      label {
        &.choose {
          font-size: 18px;
          margin: 12px 0 20px 0;
        }
        &:not(.choose) {
          font-size: 14px;
        }
      }
      input {
        &.checkInput {
          margin: 35px 0;
          padding: 6px;
          font-size: 12px;
        }
      }
      .iconSwitch {
        font-size: 20px;
      }
    }

    table {
      margin: 40px 0;
      thead {
        tr {
          th {
            padding: 15px 10px;
            font-size: 12px;
          }
        }
      }
      tbody {
        tr {
          td {
            padding: 10px 5px;
            button {
              padding: 4px 16px;
              font-size: 13px;
            }
          }
        }
      }
    }
    .clearStudentList {
      padding: 5px 0;
      font-size: 35px;
      & > div {
        span {
          font-size: 12px;
        }
        div {
          button {
            font-size: 12px;
            padding: 5px 15px;
            margin: 8px;
          }
        }
      }
    }
  }
}
</style>
