import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./sass/mainSass.scss";
import "normalize.css";

export const allFunc = new Vue({
  data() {
    return {
      studentList: [],
      updataStudent: null,
      place: null
    };
  },
  methods: {
    addStudent(name, id) {
      name = this.firstLetterCapital(name);
      this.studentList.push({ id, name });
      localStorage.setItem("myAllStudents", JSON.stringify(this.studentList));
    },
    firstLetterCapital(name) {
      let allName = name.split(" "),
        nameSeparation1 = "",
        nameSeparation2 = "",
        nameSeparation3 = "",
        nameSeparation4 = "",
        n = "",
        space,
        count = 0,
        condition = true;

      while (condition) {
        if (allName.includes("")) {
          let index = allName.indexOf("");
          allName.splice(index, 1);
        } else {
          condition = false;
        }
      }
      allName.splice(0, count);

      for (let i = 0; i < allName.length; i++) {
        nameSeparation1 = allName[i].split("");
        nameSeparation2 = nameSeparation1[0].toUpperCase();
        nameSeparation3 = nameSeparation1
          .slice(1)
          .join("")
          .toLowerCase();
        nameSeparation4 = nameSeparation2 + nameSeparation3;
        i == allName.length - 1 ? (space = "") : (space = " ");
        n += nameSeparation4 + space;
      }
      return n;
    },
    getInfo(index) {
      this.updataStudent = this.studentList[index];
      this.place = index;
    },
    finalUpdate(myName, myId) {
      this.studentList[this.place].name = myName;
      this.studentList[this.place].id = myId;
      localStorage.setItem("myAllStudents", JSON.stringify(this.studentList));
    }
  }
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
